<template>
  <section class="users">
    <!-- SWEET MODALS -->
    <sweet-modal ref="userDocument" title="User Document">
      <h3>
        Document
      </h3>
    </sweet-modal>

    <sweet-modal ref="userPic" title="User Picture">
      <div class="modal-pic">
        <img src="@/assets/driver.jpg" alt="" />
      </div>
    </sweet-modal>

      <div class="container">
        <div class="top">
          <div class="page-heading">
            <h4>Dashboard > {{ $route.name }}</h4>
          </div>
        </div>
        <div class="main-data">
          <div class="table-responsive">
            <table class="table table-hover table-striped">
              <thead class="thead-colored">
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Last Name</th>
                  <th scope="col">First Name</th>
                  <th scope="col">Image</th>
                  <th scope="col">E-mail</th>
                  <th scope="col">Document</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>
              <transition name="fade">
                <tbody v-if="users">
                  <tr v-for="(user, index) in users" :key="index">
                    <td scope="row">{{index}}</td>
                    <td scope="row">Last Name</td>
                    <td>First Name</td>
                    <td @click="showSweetModalPic()">
                      <div class="table-img">
                        <img src="@/assets/driver.jpg" alt="" />
                      </div>
                    </td>
                    <td>E-mail</td>
                    <td class="document-td" @click="showSweetModalDoc()">
                      View Document
                    </td>
                    <td class="form-btn">
                     Actions
                    </td>
                  </tr>
                </tbody>
              </transition>
            </table>
          </div>
          <div class="loading-data" v-if="!users && !error">
            <p>Loading</p>
            <RotateSquare2></RotateSquare2>
          </div>
          <div v-if="error">
            <p>
              {{ error }}
            </p>
          </div>
        </div>
      </div>
  </section>
</template>

<script>
export default {
  components: {
  },
  data() {
    return {
      // API ERROR VARIBLES
      error: undefined,
      users: [0, 1, 2, 3, 4],
      // SEARCH VARIABLES
      searchById: "",
      searchByName: "",
    };
  },
  methods: {
    showSweetModalDoc() {
      this.$refs.userDocument.open();
    },
    showSweetModalPic() {
      this.$refs.userPic.open();
    },
  },
};
</script>

<style lang="scss" scoped>
.thead-colored {
  background-color: $primary;
  color: $white;
}

// DOCUMENT
.document-td {
  cursor: pointer;
}
// ICONS
.table-icons {
  .eye-icon,
  .chart-icon {
    margin: 1px;
    color: $text;
    cursor: pointer;
  }
}

// TABLE IMGVARIBLES
.table-img {
  width: 100px;
  margin: 0 auto;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 100%;
  }
}

// MODAL
.modal-pic {
  width: 100%;
  img {
    width: 100%;
  }
}
</style>
